  padding-top: 13px;
  padding-bottom: 15px; }

.phone-link {
  font-weight: 600;
  line-height: 20px;
  margin-right: 16px; }

.operation-mode,
.callback-link {
  font-size: 14px;
  line-height: 20px;
  color: #1b1a1f; }

.callback-link {
  transition: .3s ease;
  border-bottom: 1px dashed rgba(27, 26, 31, 0.3); }

.callback-link:hover {
  border-bottom-color: rgba(27, 26, 31, 0.6); }

.operation-mode {
  margin-right: 24px;
